/******************
 * Type Selectors *
 ******************/
 
 @font-face {
   font-family: 'Pixel';
   src: url(/static/media/pixel_emulator.ac0c25b4.otf);
 }

:root {
  /* Theme colors */
  --primary-color: #D09B2C;
  --tint-color-end: #D09B2C70;
  --tint-color-start: #770000D0;
  --bg-color-light: #ECE2E2;
  --bg-color-dark: black;
  --font-color-light: white;
  --font-color-dark: #313338;

  /* Font stacks */
  --text-font: 'Open Sans', sans-serif;
  --header-font: 'Pixel', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-size: 12pt;
  background-color: white;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-family: 'Pixel', sans-serif;
  font-family: var(--header-font);
  font-size: inherit;
}

/*
 * Block
 */

.navbar {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width: 96vw;
  padding: 0.2em 2vw;
  background-color: var(--bg-color-dark);
  color: var(--font-color-light);
  font-size: 24pt;
}

/*
 * Element
 */

.navbar__logo {
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.navbar__menu {
  text-align: right;
}

.navbar__list {
  font: bolder 0.5em var(--text-font);
  display: -webkit-inline-flex;
  display: inline-flex;
  color: inherit;
}

.navbar__list-item {
  margin: 0.3em;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

/*
 * Modifier
 */

.navbar__list--dropdown {
  display: block;
}

/*
 * Block
 */

.text {
  font-family: var(--text-font);
  font-size: inherit;
  color: inherit;
}

.text:first-of-type {
  margin-top: 0;
}

.text:last-of-type {
  margin-bottom: 0;
}

/*
 * Modifier
 */

.text--accent {
  color: var(--primary-color);
}

.text--big {
  font-weight: bold;
  font-size: 15pt;
}

.text--bigger {
  font-weight: bold;
  font-size: 24pt;
}

/*
 * Block
 */

.clickable {
  cursor: pointer;
  text-decoration: underline;
  display: inline-block;
  color: unset;
}

/*
 * Modifier
 */

.clickable--border {
  border: solid 2px;
  padding: 0.3em;
  text-decoration: none;
}

.clickable--nav-link {
  display: block;
  font-family: var(--text-font);
  text-transform: uppercase;
  text-decoration: none;
}

.clickable--nav-link:hover,
.clickable--border:hover {
  color: var(--primary-color);
}

/*
 * Block
 */

.mlh-badge {
  display: block;
  max-width: 100px;
  min-width: 60px;
  position: fixed;
  right: 50px;
  top: 0;
  width: 10%;
  z-index: 10000;
}

/*
 * Element
 */

.mlh-badge__image {
  width: 100%;
}

/*
 * Block
 */

.section {
  margin: 2.4em 0;
  padding: 0 10%;
}

.section:first-of-type {
  margin-top: 0;
}

.section:last-of-type {
  margin-bottom: 0;
}

/*
 * Element
 */

.section__header {
  position: relative;
  left: -3%;
  font-size: 36pt;
  display: inline-block;
  margin-bottom: 1em;
  border-bottom: 0.1em solid var(--primary-color);
}

/*
* Modifier
*/

.section--main {
  margin: 0;
  font: 20pt bold;
  text-align: center;
  min-height: 100vh;
  background-image:
    linear-gradient(
      var(--tint-color-start),
      var(--tint-color-end)),
    url(/static/media/dragon_arcade.5ddb47bc.png);
  background-position: center;
  background-size: cover;
  color: var(--font-color-light);
}

.section--main::before {
  content: "";
  padding-top: 20vh;
  display: block;
}

.section--main::after {
  content: "";
  padding-bottom: 16px;
  display: block;
}

.section__header--main {
  position: unset;
  left: unset;
  font-size: 64pt;
  border-bottom: unset;
  margin-bottom: 0.75em;
}

@media (max-width: 920px) {
  .section--main {
    font-size: 18pt;
  }

  .section__header--main {
    font-size: 48pt;
  }
}

@media (max-width: 687px) {
  .section--main {
    font-size: 15pt;
  }

  .section__header--main {
    font-size: 36pt;
  }
}

@media (max-width: 517px) {
  .section__header--main {
    font-size: 28pt;
  }

  .section--main {
    font-size: 12pt;
  }
}

@media (max-width: 400px) {
  .section__header--main {
    font-size: 24pt;
  }
}

/*
 * Block
 */

.logo {
  display: block;
}

/*
 * Element
 */

.logo__image {
  width: 200px;
}

/*
 * Modifiers
 */

.logo--mlh {
  margin: 20px;
}

/*
 * Block
 */

.sponsor-tier {
  margin: 40px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

/*
 * Element
 */

/* Broke BEM guidelines for sanity */
.sponsor-tier > * {
  margin: 30px;
}

/*
 * Block
 */

.faq {
  margin: 2em 0;
}

.faq:first-of-type {
  margin-top: 0;
}

/*
 * Element
 */

.faq__question {
  font-size: 18pt;
  margin-bottom: 0.2em;
}

@media (max-width: 510px) {
  .faq__question {
    font-size: 14pt;
  }
}

/*
 * Block
 */

.schedule {
  margin: 10px 20px;
}

/*
 * Element
 */

.schedule__title {
  font-family: var(--header-font);
  font-size: 18pt;
  text-align: center;
  margin-bottom: 12px;
}

.schedule__header {
  font-family: var(--header-font);
  font-size: 16pt;
  border-bottom: thin solid black;
}

.schedule__row-cell {
  padding: 3px 40px;

  border-bottom: thin solid black;
}

/*
 * Modifier
 */

 .schedule__row-cell--time {
   font-weight: bold;
 }

